<template>
  <div class="update">
    <h2>Change the recipie</h2>
    <form>
      <input
        type="text"
        v-model="recipie.name"
        placeholder="Type a name of the recipie"
        required
      />
      <input
        type="text"
        v-model="recipie.type"
        placeholder="Type of the recipie"
        required
      />
      <input
        type="text"
        v-model="recipie.time"
        placeholder="Time of the recipie"
        required
      />
      <textarea
      v-model="recipie.description"
      placeholder="Type a description here"
      required
      />
      <!--<input
        type="file"
        ref="fileInput"
        accept="image/*"
        v-on:change="previewImage"
      />
      
       <button class="choose-image" type="button" v-on:click="triggerChooseImg">
        Choose Image
      </button>

      <div>
        <img :src="recipie.image" class="image-preview" />
      </div> -->
      <button type="button" class="button-save-recipie" v-on:click="updateRecipie">Save the recipie</button>
    </form>
  </div>
</template>

<script>
import { postRef } from "../firebase-db";
export default {
  name: "Update",
  props: {
    recipie: Object
  },
  methods: {
    triggerChooseImg() {
      this.$refs.fileInput.click();
    },
    previewImage() {
      const imageFile = this.$refs.fileInput.files[0];
      const fileReader = new FileReader();
      fileReader.onload = event => {
        this.recipie.image = event.target.result;
      };
      fileReader.readAsDataURL(imageFile);
    },
    updateRecipie() {
      postRef.doc(this.recipie.id).set({
        name: this.recipie.name,
        type: this.recipie.type,
        description: this.recipie.description
      });

      this.$router.push("/");
    }
  }
};
</script>

<style scoped>
form {
  padding: 0 1em 2.5em;
}

form> input {
  height: 40px;
  font-size: 1em;
}

textarea {
  width: 100%;
  height: 200px;
  border: none;
  box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.15);
  border-radius: 16px;
  font-family: roboto;
  font-style: normal;
}

button.choose-image {
  background-color: #d89e20;
  width: 100%;
}

input {
  margin: 1.5em auto;
  width: 100%;
  max-width: 350px;
  padding: 12px 15px;
  box-sizing: border-box;
  display: block;
}

input[type="file"] {
  display: none;
}

.image-preview {
  max-width: 350px;
  width: 100%;
  padding: 1em 0;
}
</style>
